.container {
  position: fixed; }

.division {
  margin: 25px;
  width: 460px;
  height: 280px;
  background-color: #454E54;
  position: relative;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  padding: 25px; }

.navHeading {
  color: white;
  font-size: 37px;
  margin-left: 35px; }

.sidebar {
  padding: 0 0.5rem;
  width: 100%;
  padding-bottom: 1.5rem; }
  .sidebarEl {
    padding: 0.2rem 0;
    font-size: 16px;
    color: white;
    margin: 0.2rem 0;
    display: flex;
    width: 100%;
    align-items: center; }
    .sidebarEl:hover {
      background-color: #1E2024;
      cursor: pointer;
      color: #f2f1ef;
      border-radius: 6px; }
  .sidebar_icon {
    font-size: 25px;
    margin: 10px 25px;
    color: white; }

.user {
  display: flex;
  flex-basis: row;
  vertical-align: middle;
  margin: 0 0 15px 0; }

.ppicture {
  height: 70px;
  width: 70px;
  border-radius: 35px;
  margin-right: 15px;
  object-fit: cover; }

.username {
  display: block;
  color: white;
  font-size: 28px;
  line-height: 37px; }

.status {
  display: block;
  color: #C6C6D9;
  font-size: 16px;
  line-height: 27px; }

.online {
  position: absolute;
  top: 70px;
  left: 70px;
  height: 30px;
  width: 30px;
  border-radius: 50%;
  background-color: #66BB6A;
  border: solid #454E54 7px; }
